<template>
  <div>
    <Navbar>
      <template v-slot:left>
        <div class="icon"><i class="iconfont icon-jiantou"></i></div>
      </template>
      <template v-slot:default> 用户注册 </template>
      <template v-slot:right> </template>
    </Navbar>

    <div style="margin-top: 50px">
      <div style="text-align: center; padding-top: 50px">
        <van-image
          width="10rem"
          height="10rem"
          fit="contain"
          :src="require('assets/logo.png')"
        />
      </div>
    </div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="name"
        name="用户名"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <van-field
        v-model="password_confirmation"
        type="password"
        name="确认密码"
        label="确认密码"
        placeholder="确认密码"
        :rules="[{ required: true, message: '请填写一致密码' }]"
      />
      <van-field
        v-model="email"
        name="邮箱"
        label="邮箱"
        placeholder="邮箱"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <div style="margin: 16px">
        <van-button
          round
          block
          type="info"
          style="border: 1px solid var(--color-tint)"
          color="var(--color-tint)"
          native-type="submit"
          >注册</van-button
        >
        <div class="link-login" @click="router.push({ path: '/login' })">
          已有账号
        </div>
      </div>
    </van-form>
  </div>
</template>

<script>
import Navbar from "components/common/navbar/Navbar";
import { register } from "network/user";
import { toRefs, reactive } from "vue";
import { Dialog, Toast } from "vant";
import { useRouter } from "vue-router";

export default {
  name: "Register",
  components: {
    Navbar,
  },
  setup() {
    const router = useRouter();
    const userinfo = reactive({
      name: "",
      password: "",
      password_confirmation: "",
      email: "",
    });

    const onSubmit = () => {
      // 验证
      if (userinfo.password != userinfo.password_confirmation) {
        Dialog.alert({
          message: "两次密码输入不一致",
          confirmButtonColor: "var(--color-tint)",
        }).then(() => {
          // on close
        });
      } else {
        register(userinfo).then((res) => {
          if (res.status == "201") {
            Toast.success("注册成功");
            setTimeout(() => {
              router.push({ path: "/login" });
            }, 500);
          }
          userinfo.password_confirmation = "";
          userinfo.password = "";
        });
      }
      // 提交给服务器
    };

    return {
      ...toRefs(userinfo),
      onSubmit,
      router,
    };
  },
};
</script>

<style>
.link-login {
  font-size: 14px;
  margin-top: 20px;
  color: var(--color-tint);
  display: inline-block;
  text-align: left;
}
</style>